.footer {
	
	text-align: center;
	background-color: $verdeItalia;
	font-family: $fuenteSecundaria;
	margin-top: 2rem;

	.redes-sociales {
		display: flex;
		justify-content: space-evenly;
		align-items: center;	
	}


	.autor {
		padding-bottom: 2rem;
		font-size: 18px;
		color: $blanco;
	}

}

.redes-sociales a {
	margin-top: 2rem;
	text-decoration: none;
	text-align: center;
	font-size: 22px;
	width: 30px;
	height: 30px;
	line-height: 30px;
	background-color: skyblue;
	border-radius: 50%;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, .5);
	transition: all .4s ease-in-out;


	&:hover {
		transform: scale(1.2);
		background-color: darkseagreen;
	}


}



