.header {

	background-color: $verde; 

	.contenido-header {

		padding: 1rem 0;
		
		@include telefono {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}


	.imagen {

		width: $separacion * 2;
		height: $separacion * 2;
		margin: $separacion * 0.10 $separacion * 2.2 $separacion * 0.2 $separacion * 1.2;


	}

	.navegacion-principal {

		display: flex;
		gap : 1.2rem;
		margin-bottom: 0.5rem;

		width: 50%;

	}


	a {
		color: blue;
		margin: 4px;
		font-size: 2rem;

		&:hover {
			color: $rojoItalia;
		}
	}

	&.fijo {
		position: fixed;
		width: 100%;
		padding: 5rem auto;
		box-shadow: 0 .3rem .4rem #4444;

	}

}


