.servicios {

	h2 {

		font-size: 2rem;
		font-family: $fuenteSecundaria;
		color: $colorNuevo;
		padding-left: 2rem;
	}

	.datos-personales {

		font-size: 1.7rem;
		padding-left: 2rem;

	}


}

.servicios-iconos {
	display: flex;
	flex-direction: column;
	align-items: center;

	h3 {

		font-size: 1.8rem;
		color: $secundario;
		font-weight: normal;
		font-family: $fuenteSecundaria;
	}

	p {

		line-height: 2;
		font-size: 1.5rem;
		font-family: sans-serif;
		text-align: center;
	}

}

.iconos {

	height: 12rem;
	width: 12rem;
	background-color: $primario;
	border-radius: 50%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	.icono {
		height: 6rem;
		width: 6rem;
	}

}

.titulo-servicios {

	h2 {

		font-size: 2rem;
		font-family: $fuenteSecundaria;
		color: $colorNuevo;
		padding-left: $separacion;

	}
		
}


.elenco-servicios {

	padding: $separacion * 0.5;

	h2 {

		font-size: 2rem;
		font-family: $fuenteSecundaria;
		color: $colorNuevo;
		padding-left: 2rem;

	}

	@include tablet {
		@include grid(4, $separacion);
	}

}