.ubicacion {

	h2 {

		font-family: $fuenteSecundaria;
		color: $colorNuevo;
		font-size: 2.2rem;
		padding-left: $separacion;

	}

	.caja-formulario {


		@include tablet {
			@include grid (2, $separacion*0.5);
		}

	}

	.formulario {
		width: 90%;
		padding: $separacion;
		display: grid;
		gap:0.5em;
		grid-auto-columns: 100%	;

	}
} 