html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Kanit", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-color: #4CB8B3;
}
.header .contenido-header {
  padding: 1rem 0;
}
@media (min-width: 480px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .imagen {
  width: 10rem;
  height: 10rem;
  margin: 0.5rem 11rem 1rem 6rem;
}
.header .navegacion-principal {
  display: flex;
  gap: 1.2rem;
  margin-bottom: 0.5rem;
  width: 50%;
}
.header a {
  color: blue;
  margin: 4px;
  font-size: 2rem;
}
.header a:hover {
  color: #CE2B37;
}
.header.fijo {
  position: fixed;
  width: 100%;
  padding: 5rem auto;
  box-shadow: 0 0.3rem 0.4rem rgba(68, 68, 68, 0.2666666667);
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}
.contenido-video h1 {
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #FFF;
  font-size: 3.5rem;
}

.sobre-mi {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-mi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.sobre-mi .imagen-personal {
  padding-left: 18rem;
}

.contenido-personal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 5rem;
}
.contenido-personal h2 {
  color: #CE2B37;
  font-size: 2.2rem;
  font-family: "Lobster";
}
.contenido-personal .texto {
  font-size: 1.7rem;
  line-height: 2.5rem;
}

.servicios h2 {
  font-size: 2rem;
  font-family: "Lobster";
  color: #FF8A65;
  padding-left: 2rem;
}
.servicios .datos-personales {
  font-size: 1.7rem;
  padding-left: 2rem;
}

.servicios-iconos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.servicios-iconos h3 {
  font-size: 1.8rem;
  color: #0097A7;
  font-weight: normal;
  font-family: "Lobster";
}
.servicios-iconos p {
  line-height: 2;
  font-size: 1.5rem;
  font-family: sans-serif;
  text-align: center;
}

.iconos {
  height: 12rem;
  width: 12rem;
  background-color: #FFC107;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.iconos .icono {
  height: 6rem;
  width: 6rem;
}

.titulo-servicios h2 {
  font-size: 2rem;
  font-family: "Lobster";
  color: #FF8A65;
  padding-left: 5rem;
}

.elenco-servicios {
  padding: 2.5rem;
}
.elenco-servicios h2 {
  font-size: 2rem;
  font-family: "Lobster";
  color: #FF8A65;
  padding-left: 2rem;
}
@media (min-width: 768px) {
  .elenco-servicios {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
  }
}

.portfolio h2 {
  color: #FF8A65;
  font-family: "Lobster";
  font-size: 2.2rem;
  padding-left: 2.5rem;
}
.portfolio ul {
  list-style: none;
}
.portfolio .imagenes {
  width: 35rem;
  height: 32rem;
  padding: 5rem;
}
.portfolio a {
  text-align: center;
}

.ubicacion h2 {
  font-family: "Lobster";
  color: #FF8A65;
  font-size: 2.2rem;
  padding-left: 5rem;
}
@media (min-width: 768px) {
  .ubicacion .caja-formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
  }
}
.ubicacion .formulario {
  width: 90%;
  padding: 5rem;
  display: grid;
  gap: 0.5em;
  grid-auto-columns: 100%;
}

.footer {
  text-align: center;
  background-color: #009246;
  font-family: "Lobster";
  margin-top: 2rem;
}
.footer .redes-sociales {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer .autor {
  padding-bottom: 2rem;
  font-size: 18px;
  color: #FFF;
}

.redes-sociales a {
  margin-top: 2rem;
  text-decoration: none;
  text-align: center;
  font-size: 22px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  background-color: skyblue;
  border-radius: 50%;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  transition: all 0.4s ease-in-out;
}
.redes-sociales a:hover {
  transform: scale(1.2);
  background-color: darkseagreen;
}