.sobre-mi {

	padding: $separacion 0;

	@include tablet {
		@include grid(2, $separacion * 0.2);
	}

	.imagen-personal {

		padding-left: 18rem;
	}
}

.contenido-personal {
	

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding-right: $separacion;
	
	h2 {

		color: $rojoItalia;
		font-size: 2.2rem;
		font-family: $fuenteSecundaria;
	}


	.texto {
		font-size: 1.7rem;
		line-height: 2.5rem;
	}
}
